*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.skill {
    position: absolute;
    /* animation: fade 2s ease-out; */
}
.skill1 {
    right: 14%;
    bottom: 10%;
    font-size: 0.9em;
}

.skill2 {
    top: 35%;
    left: 0; 
    right: 0; 
    font-size: 0.8em;
    margin-left: auto; 
    margin-right: auto;
    position: relative;
}

.skill2 .pentagon p {
    transform: rotate(180deg);
    position: absolute;
    top: 40%;
    left: 0;
    right: 0;
    margin: auto;
}

.skill3 {
    bottom: 10%;
    left: 14%;
    /* transform: rotate(45deg); */
}

.skill4 {
    top:20%;
    left: 13%;
}

.skill5 {
    top:41%;
    left: 24%;
    font-size: 0.9em;

}

.skill6 {
    word-break:break-word;
    word-spacing: 3em;
    top: 13%;
    left: 33%;
}

.skill7 {
    word-break:break-word;
    font-size: 1em;
    word-spacing: 5em;
    top: 41%;
    right: 24%;
}

.skill8 {
    top: 48%;
    left: 8%;
}

.skill9 {
    top:20%;
    right: 13%;
}

.skill10 {
    top: 48%;
    right: 8%;
    font-size: 1em;
}
.skill11 {
    top:13%;
    right: 33%;
}

@keyframes fade {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }