.fade-enter {
    opacity: 0;
    z-index: 1;
    position: absolute;
    left: 0;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    max-width: 1px;
    max-height: 1px;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0;
    -webkit-transition: opacity 300ms linear;
    transition: opacity 300ms linear;
}

.fade-exit-done {
    opacity: 0;
}

.back-button {
    position: absolute;
    top: 50%;
    left: 0;
    cursor: pointer;
}
.forward-button {
    position: absolute;
    top: 50%;
    right: 0;
    cursor: pointer;
}
